<template>
  <div class="flex-1 baidu-maps"
      v-loading="loading"
      element-loading-text="加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
    <div class="scroll result">
      <div>
        {{status}}-{{result}}
      </div>
      <div class="result-item" :key="item.title" v-for="item in geocoderResult">
        <div class="result__title">{{item.title}}</div>
        <div class="result__address">{{item.address}}</div>
      </div>
    </div>
    <el-amap class="amap-box"
      :center="amapConfig.center"
      :zoom="amapConfig.zoom"
      :dragEnable="amapConfig.dragEnable"
      :zoomEnable="amapConfig.zoomEnable"
      :doubleClickZoom="amapConfig.doubleClickZoom"
      :keyboardEnable="amapConfig.keyboardEnable"
      :scrollWheel="amapConfig.scrollWheel"
      :touchZoom="amapConfig.touchZoom"
      :plugin="plugin"
    >
      <el-amap-marker :position="amapConfig.center"></el-amap-marker>
      <el-amap-circle :center="amapConfig.center" :radius="500" :fill-opacity=".5"></el-amap-circle>
    </el-amap>
  </div>
</template>

<script>
export default {
  name: 'BaiduMaps',
  components: {
  },
  data () {
    const self = this
    return {
      status: '6',
      result: '',
      loading: false,
      geocoderResult: {},
      amapConfig: {
        center: [116.403963, 39.915119],
        zoom: 15,
        dragEnable: false,
        zoomEnable: false,
        doubleClickZoom: false,
        keyboardEnable: false,
        scrollWheel: false,
        touchZoom: false
      },
      plugin: [{
        pName: 'Geolocation',
        events: {
          init (o) {
            // o 是高德地图定位插件实例
            o.getCurrentPosition((status, result) => {
              self.status = status
              self.result = result
              alert(status)
              if (result && result.position) {
                self.amapConfig.center = [self.lng, self.lat]
                self.$nextTick()
              }
            }, {
              enableHighAccuracy: true,
              maximumAge: 1000
            })
          }
        }
      }]
    }
  },
  methods: {
  }
}
</script>

<style scoped lang="less">
.amap-box {
  height: 100%;
  ::v-deep .amap-logo,
  ::v-deep .amap-copyright {
    visibility: hidden!important;
  }
}

.result {
  max-height: 30vh;
  padding: 10px;
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  z-index: 2;
  background: rgba(255,255,255,.9);
  overflow: auto;
}
.result-item + .result-item {
  margin-top: 10px;
}
.result__title {
  font-weight: bold;
}
.result__address {}
</style>
